//  DungeonTest.jsx - 
import React, { useState, useEffect, useRef } from 'react';
import { getShopItems, buyItem } from '../services/inventoryShopService.js';
import {
  enterDungeon,
  exploreCurrentFloor,
  summarizeExploration
} from '../services/dungeonTestService.js';
import axios from 'axios';
import StatAllocation from '../components/game/StatAllocation.js';
import CombatSystem from '../components/game/CombatSystem';

// 
const GAME_STATES = {
  IDLE: 'idle',
  ENTERING_DUNGEON: 'entering_dungeon',
  EXPLORING: 'exploring',
  COMBAT: 'combat',
  SHOP: 'shop',
  VICTORY: 'victory',
  STATS_ALLOCATION: 'stats_allocation'
};

// 
const DEBUG = true;

// 
const ShopInterface = ({ items, gold, onBuyItem, onLeaveShop }) => {
  return (
    <div style={{ 
      padding: '20px', 
      backgroundColor: '#3a1f6b', 
      borderRadius: '12px', 
      border: '2px solid #5d3494',
      color: '#e0e0e0'
    }}>
      <h3 style={{ textAlign: 'center', color: '#ffffff', marginBottom: '15px' }}>
         
      </h3>
      
      <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '15px' }}>
        <div style={{ 
          backgroundColor: '#ffa726', 
          padding: '8px 12px', 
          borderRadius: '6px',
          display: 'flex',
          alignItems: 'center',
          boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
          color: '#2c1810',
          border: '2px solid #ff8f00'
        }}>
          <span style={{ marginRight: '4px' }}></span>
          <span style={{ fontWeight: 'bold' }}>{gold} </span>
        </div>
      </div>
      
      <div style={{ maxHeight: '300px', overflowY: 'auto' }}>
        {!Array.isArray(items) || items.length === 0 ? (
          <div style={{ 
            textAlign: 'center', 
            padding: '20px', 
            color: '#b89be6',
            backgroundColor: '#2c1810',
            borderRadius: '8px',
            border: '1px solid #5d3494'
          }}>
            
          </div>
        ) : (
          <div style={{ display: 'grid', gridTemplateColumns: '1fr', gap: '15px' }}>
            {items.map(entry => (
              <div key={entry._id || `item-${Math.random()}`} style={{
                border: '2px solid #5d3494',
                borderRadius: '8px',
                padding: '15px',
                backgroundColor: '#2c1810',
                transition: 'all 0.2s ease'
              }}>
                <div style={{ display: 'flex' }}>
                  <div style={{ 
                    width: '50px', 
                    height: '50px', 
                    backgroundColor: '#4c2a85',
                    borderRadius: '8px',
                    marginRight: '15px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    fontSize: '24px',
                    border: '2px solid #7e4ab8'
                  }}>
                    {entry.item?.icon ? '' : ''}
                  </div>
                  <div style={{ flex: 1 }}>
                    <div style={{ fontWeight: 'bold', marginBottom: '5px', color: '#ffffff' }}>
                      {entry.item?.name || ''}
                    </div>
                    <div style={{ fontSize: '14px', color: '#b89be6', marginBottom: '8px' }}>
                      {entry.item?.description || ''}
                    </div>
                    <div style={{ 
                      display: 'flex', 
                      justifyContent: 'space-between', 
                      alignItems: 'center',
                      marginTop: '10px'
                    }}>
                      <span style={{ fontWeight: 'bold', color: '#ffa726' }}>
                        {entry.price} 
                      </span>
                      <button 
                        onClick={() => onBuyItem(entry.item?._id, entry.price)}
                        style={{
                          padding: '6px 12px',
                          backgroundColor: gold >= entry.price ? '#4caf50' : '#666',
                          color: 'white',
                          border: 'none',
                          borderRadius: '6px',
                          cursor: gold >= entry.price ? 'pointer' : 'not-allowed',
                          fontFamily: 'Courier New, monospace',
                          fontWeight: 'bold',
                          transition: 'all 0.2s ease'
                        }}
                        disabled={gold < entry.price || !entry.item?._id}
                      >
                        
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
      
      <button 
        onClick={onLeaveShop}
        style={{
          marginTop: '20px',
          padding: '10px 20px',
          backgroundColor: '#ff9800',
          color: 'white',
          border: 'none',
          borderRadius: '6px',
          cursor: 'pointer',
          fontSize: '16px',
          fontFamily: 'Courier New, monospace',
          fontWeight: 'bold',
          display: 'block',
          margin: '20px auto 0',
          border: '2px solid #f57800',
          transition: 'all 0.2s ease'
        }}
        onMouseOver={(e) => {
          e.target.style.backgroundColor = '#f57800';
          e.target.style.transform = 'translateY(-1px)';
        }}
        onMouseOut={(e) => {
          e.target.style.backgroundColor = '#ff9800';
          e.target.style.transform = 'translateY(0)';
        }}
      >
        
      </button>
    </div>
  );
};

//  - 
const DungeonTest = ({ userStats, onGoldUpdate, gold  }) => {
  const [gameState, setGameState] = useState(GAME_STATES.IDLE);
  const [logs, setLogs] = useState([]);
  const [summary, setSummary] = useState(null);
  const [error, setError] = useState(null);
  const [currentFloor, setCurrentFloor] = useState(1);
  const [shopItems, setShopItems] = useState([]);
  const [monsters, setMonsters] = useState([]);
  const [playerStats, setPlayerStats] = useState({
      hp: userStats?.baseStats?.hp || 100,
      attack: userStats?.baseStats?.attack || 10,
      defense: userStats?.baseStats?.defense || 5,
      magicPower: userStats?.baseStats?.magicPower || 0,
      speed: userStats?.baseStats?.speed || 0,
      critRate: userStats?.baseStats?.critRate || 5,
      evasion: userStats?.baseStats?.evasion || 0
    });
  useEffect(() => {
  if (userStats?.baseStats) {
    setPlayerStats({
      hp: userStats.baseStats.hp || 100,
      attack: userStats.baseStats.attack || 10,
      defense: userStats.baseStats.defense || 5,
      magicPower: userStats.baseStats.magicPower || 0,
      speed: userStats.baseStats.speed || 0,
      critRate: userStats.baseStats.critRate || 5,
      evasion: userStats.baseStats.evasion || 0
    });
  }
}, [userStats]);
  
  // 
  const logsEndRef = useRef(null);
  const prevStateRef = useRef(null);
  const transitionInProgressRef = useRef(false);
  
  const userInfo = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : null;
  const token = userInfo?.token || null;

  // 
  useEffect(() => {
    if (logsEndRef.current) {
      logsEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [logs]);
  
  // 
  useEffect(() => {
    if (DEBUG) console.log(`: ${gameState}`);
    
    // 
    if (gameState === GAME_STATES.EXPLORING && prevStateRef.current === GAME_STATES.SHOP) {
      if (DEBUG) console.log('');
      
      // 
      if (monsters && monsters.length > 0) {
        if (DEBUG) console.log('');
        setTimeout(() => {
          setGameState(GAME_STATES.COMBAT);
        }, 300);
      }
    }
    
    // 
    prevStateRef.current = gameState;
  }, [gameState, monsters]);

  // 
  useEffect(() => {
    if (userStats?.baseStats) {
      setPlayerStats({
        hp: userStats.baseStats.hp || 100,
        attack: userStats.baseStats.attack || 10,
        defense: userStats.baseStats.defense || 5,
        magicPower: userStats.baseStats.magicPower || 0,
        speed: userStats.baseStats.speed || 0,
        critRate: userStats.baseStats.critRate || 5,
        evasion: userStats.baseStats.evasion || 0
      });
    }
  }, [userStats]);

  // 
  const loadShopItems = async () => {
    try {
      console.log("...");
      const items = await getShopItems(token);
      console.log(":", items);
      
      if (items) {
        setShopItems(Array.isArray(items) ? items : []);
      } else {
        setShopItems([]);
      }
    } catch (err) {
      console.error(':', err);
      setShopItems([]);
    }
  };

  // 
  const handleBuyItem = async (itemId, price) => {
    try {
      // 
      const res = await axios.get('/api/users/profile', {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      if (res.data.gold < price) {
        alert('');
        return;
      }
      
      await axios.post(
        '/api/shop/buy', 
        { itemId }, 
        { headers: { Authorization: `Bearer ${token}` }}
      );
      
      // 
      setLogs(prev => [...prev, `  ${shopItems.find(i => i.item._id === itemId)?.item.name || ''}`]);
      
      // 
      if (onGoldUpdate) {
        onGoldUpdate();
      }
    } catch (err) {
      alert(`: ${err.message}`);
    }
  };

  // 
  const handleCombatEnd = async (result) => {
    console.log(":", result);
    
    if (result.result === 'victory') {
      // 
      setLogs(prev => [...prev, ' ']);
      
      // 
      if (result.drops) {
        const { gold, exp, items, cards } = result.drops;
        
        // 
        if (gold > 0 && onGoldUpdate) {
          onGoldUpdate();
        }
        
        setLogs(prev => [...prev, ' ']);
      }
      
      setPlayerStats(prev => ({
        ...prev,
        hp: result.remainingHp
      }));
      
      try {
        // 
        const updateResponse = await axios.post(
          '/api/dungeon/update-after-combat',
          { 
            result: 'victory', 
            remainingHp: result.remainingHp 
          },
          { headers: { Authorization: `Bearer ${token}` }}
        );
        
        console.log(':', updateResponse.data);
        
        // 
        if (updateResponse.data.logs && Array.isArray(updateResponse.data.logs)) {
          setLogs(prev => [...prev, ...updateResponse.data.logs]);
        }
        
        if (updateResponse.data.expGained) {
          setLogs(prev => [...prev, `  ${updateResponse.data.expGained} `]);
        }

        if (updateResponse.data.levelUp) {
          setLogs(prev => [
            ...prev, 
            `  ${updateResponse.data.prevLevel || '?'}  ${updateResponse.data.currentLevel || updateResponse.data.newLevel || '?'} `
          ]);
          
          if (updateResponse.data.statPointsGained > 0) {
            setLogs(prev => [
              ...prev,
              `  ${updateResponse.data.statPointsGained} `
            ]);
          }
        }
        
        // 
        if (updateResponse.data.nextFloor) {
          console.log(`: ${currentFloor} -> ${updateResponse.data.nextFloor}`);
          setCurrentFloor(updateResponse.data.nextFloor);
          setLogs(prev => [...prev, `  ${updateResponse.data.nextFloor} `]);
        }
        
        // 
        setTimeout(() => {
          continueExploration();
        }, 1000);
      } catch (err) {
        console.error(':', err);
        setTimeout(() => {
          continueExploration();
        }, 1000);
      }
    } else if (result.result === 'settlement') {
      // HP0
      setLogs(prev => [...prev, ' ...']);
      
      try {
        const summary = await summarizeExploration(token);
        setSummary(summary);
        setGameState(GAME_STATES.VICTORY);
      } catch (err) {
        console.error(':', err);
      }
    }
  };
  // 
  const handleLeaveShop = async () => {
    console.log('=== LEAVE SHOP START ===');
    setLogs(prev => [...prev, ' ...']);
    
    // 
    setShopItems([]);
    setGameState(GAME_STATES.EXPLORING);
    
    try {
      // API
      await axios.post(
        '/api/dungeon/shop-interaction', 
        { action: 'leave' }, 
        { headers: { Authorization: `Bearer ${token}` }}
      );
      
      // continue API
      const continueResponse = await axios.post(
        '/api/dungeon/continue',
        {},
        { headers: { Authorization: `Bearer ${token}` }}
      );
      
      console.log('Continue after shop response:', continueResponse.data);
      
      // 
      if (continueResponse.data.logs && Array.isArray(continueResponse.data.logs)) {
        console.log('Adding logs from response:', continueResponse.data.logs);
        setLogs(prev => [...prev, ...continueResponse.data.logs]);
      }
      
      // 
      if (continueResponse.data.currentFloor) {
        setCurrentFloor(continueResponse.data.currentFloor);
      }
      
      // 
      if (continueResponse.data.monsters && 
          Array.isArray(continueResponse.data.monsters) && 
          continueResponse.data.monsters.length > 0) {
          
        console.log('Found monsters after shop, starting combat');
        setMonsters(continueResponse.data.monsters);
        
        // UI
        setTimeout(() => {
          setGameState(GAME_STATES.COMBAT);
        }, 300);
      } else {
        console.log('No monsters after shop, continuing exploration');
        continueExploration();
      }
    } catch (err) {
      console.error('Leave shop error:', err);
      setLogs(prev => [...prev, ` : ${err.message}`]);
      
      // 
      setTimeout(() => {
        setGameState(GAME_STATES.IDLE);
      }, 1000);
    }
    
    console.log('=== LEAVE SHOP END ===');
  };
  
  // 
  const continueExploration = async () => {
    // 
    if (transitionInProgressRef.current) {
      if (DEBUG) console.log('');
      return;
    }
    
    try {
      if (DEBUG) console.log('');
      
      // 
      setGameState(GAME_STATES.EXPLORING);
      
      // API
      const res = await exploreCurrentFloor(token);
    
      
      if (DEBUG) console.log(':', res);
      
      // 
      if (res.logs && Array.isArray(res.logs)) {
        setLogs(prev => [...prev, ...res.logs]);
      }
      
      // 
      if (res.gainedExp) {
        setLogs(prev => [...prev, `  ${res.gainedExp} `]);
      }
      
      // 
      if (res.nextFloor) {
        setCurrentFloor(res.nextFloor);
      }
      
      //  - 
      if (res.monsters && Array.isArray(res.monsters) && res.monsters.length > 0) {
        if (DEBUG) console.log('');
        setMonsters(res.monsters);
        
        // 
        setTimeout(() => {
          if (DEBUG) console.log('UI');
          setGameState(GAME_STATES.COMBAT);
        }, 300);
        return;
      }
      
      // 
      if (res.pause && res.eventType === 'shop') {
        if (DEBUG) console.log('');
        await loadShopItems();
        
        // 
        setTimeout(() => {
          if (DEBUG) console.log('UI');
          setGameState(GAME_STATES.SHOP);
        }, 300);
        return;
      }
      
      // 
      if (res.result === 'completed') {
        setLogs(prev => [...prev, ' ']);
        try {
          const summary = await summarizeExploration(token);
          setSummary(summary);
          setGameState(GAME_STATES.VICTORY);
        } catch (error) {
          console.error(':', error);
        }
      } 
      // 
      else if (res.result === 'defeat') {
        setLogs(prev => [...prev, ' ...']);
        try {
          const summary = await summarizeExploration(token);
          setSummary(summary);
          setGameState(GAME_STATES.VICTORY);
        } catch (error) {
          console.error(':', error);
        }
      } 
      // 
      else if (res.result === 'continue') {
        // 
        setTimeout(() => {
          continueExploration();
        }, 500);
      }
    } catch (err) {
      console.error(':', err);
      setLogs(prev => [...prev, ` : ${err.message}`]);
    }
  };

  // 
  const startExploration = async () => {
    setLogs([]);
    setSummary(null);
    setGameState(GAME_STATES.ENTERING_DUNGEON);

    try {
      const enter = await enterDungeon(token);
      console.log(':', enter);
      
      // 
      let initialFloor = 1;
      if (enter.exploration) {
        initialFloor = enter.exploration.floorIndex || 1;
        setCurrentFloor(initialFloor);
      }
      
      if (enter.stats) {
        setPlayerStats({
          hp: enter.stats.hp || 100,
          attack: enter.stats.attack || 10,
          defense: enter.stats.defense || 5,
          magicPower: userStats.baseStats.magicPower || 0,
          speed: userStats.baseStats.speed || 0,
          critRate: userStats.baseStats.critRate || 5,
          evasion: userStats.baseStats.evasion || 0
        });
      }
      
      // 
      setLogs([
        ` : ${enter.dungeon.name}`,
        `  ${initialFloor} `
      ]);
      
      // 
      setGameState(GAME_STATES.EXPLORING);
      continueExploration();
    } catch (err) {
      console.error(':', err);
      setLogs([` : ${err.message}`]);
      setGameState(GAME_STATES.IDLE);
    }
  };

  if (error) {
    return (
      <div style={{ 
        padding: '20px', 
        textAlign: 'center', 
        backgroundColor: '#2c1810', 
        borderRadius: '12px' 
      }}>
        <h2 style={{ color: '#e74c3c' }}></h2>
        <p style={{ color: '#e0e0e0' }}>{error}</p>
        <button 
          onClick={() => window.location.reload()}
          style={{
            padding: '10px 20px',
            backgroundColor: '#3498db',
            color: 'white',
            border: 'none',
            borderRadius: '6px',
            cursor: 'pointer',
            marginTop: '10px',
            fontFamily: 'Courier New, monospace'
          }}
        >
          
        </button>
      </div>
    );
  }

  // 
  return (
    <div style={{ 
      padding: '20px', 
      fontFamily: 'Courier New, monospace',
      color: '#e0e0e0'
    }}>
      {/*  */}
      <div style={{
        border: '2px solid #5d3494',
        borderRadius: '12px',
        padding: '15px',
        marginBottom: '20px',
        backgroundColor: '#3a1f6b',
        maxHeight: '200px',
        overflowY: 'auto'
      }}>
        <h3 style={{ 
          margin: '0 0 10px 0', 
          borderBottom: '1px solid #5d3494', 
          paddingBottom: '8px',
          color: '#ffffff'
        }}>
            -  {currentFloor} 
        </h3>
        
        {logs.length === 0 ? (
          <p style={{ color: '#b89be6', fontStyle: 'italic', textAlign: 'center' }}>
            
          </p>
        ) : (
          logs.map((log, index) => (
            <div 
              key={index}
              style={{
                padding: '6px 0',
                borderBottom: index < logs.length - 1 ? '1px solid #5d3494' : 'none',
                display: 'flex',
                alignItems: 'flex-start',
                fontSize: '14px',
                color: '#e0e0e0'
              }}
            >
              {log.includes(':') && <span style={{ marginRight: '8px' }}></span>}
              {log.includes('') && <span style={{ marginRight: '8px' }}></span>}
              {log.includes('') && <span style={{ marginRight: '8px' }}></span>}
              {log.includes('') && <span style={{ marginRight: '8px' }}></span>}
              {log.includes('') && <span style={{ marginRight: '8px' }}></span>}
              {!log.includes(':') && !log.includes('') && !log.includes('') && 
               !log.includes('') && !log.includes('') && (
                <span style={{ marginRight: '8px' }}></span>
              )}
              <span>{log}</span>
            </div>
          ))
        )}
        <div ref={logsEndRef} />
      </div>
      
      {/*  */}
      {gameState === GAME_STATES.IDLE && (
        <div style={{ textAlign: 'center', marginBottom: '20px' }}>
          <button 
            onClick={startExploration}
            style={{
              padding: '12px 25px',
              backgroundColor: '#4caf50',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              cursor: 'pointer',
              fontSize: '16px',
              fontFamily: 'Courier New, monospace',
              fontWeight: 'bold',
              border: '2px solid #388e3c',
              transition: 'all 0.2s ease'
            }}
            onMouseOver={(e) => {
              e.target.style.backgroundColor = '#388e3c';
              e.target.style.transform = 'translateY(-1px)';
            }}
            onMouseOut={(e) => {
              e.target.style.backgroundColor = '#4caf50';
              e.target.style.transform = 'translateY(0)';
            }}
          >
            
          </button>
          <p style={{ fontSize: '14px', color: '#b89be6', marginTop: '10px' }}>
            
          </p>
        </div>
      )}
      
      {gameState === GAME_STATES.ENTERING_DUNGEON && (
        <div style={{ 
          textAlign: 'center', 
          marginBottom: '20px',
          padding: '20px',
          backgroundColor: '#3a1f6b',
          borderRadius: '12px',
          border: '2px solid #5d3494'
        }}>
          <div style={{ fontSize: '24px', marginBottom: '10px' }}></div>
          <div style={{ color: '#e0e0e0' }}>...</div>
        </div>
      )}
      
      {gameState === GAME_STATES.EXPLORING && (
        <div style={{ 
          textAlign: 'center', 
          marginBottom: '20px',
          padding: '20px',
          backgroundColor: '#3a1f6b',
          borderRadius: '12px',
          border: '2px solid #5d3494'
        }}>
          <div style={{ fontSize: '24px', marginBottom: '10px' }}></div>
          <div style={{ color: '#e0e0e0' }}> {currentFloor} ...</div>
        </div>
      )}
      
      {gameState === GAME_STATES.COMBAT && (
        <CombatSystem
          monsters={monsters}
          playerStats={playerStats}
          playerClass={userStats?.classSlug || "warrior"}
          playerClassName={userStats?.name}
          skills={userStats?.skills || []} 
          userToken={token} 
          onCombatEnd={handleCombatEnd}
        />
      )}
      
      {gameState === GAME_STATES.SHOP && (
        <ShopInterface
          items={shopItems}
          gold={gold || 0}
          onBuyItem={handleBuyItem}
          onLeaveShop={handleLeaveShop}
        />
      )}
      
      {gameState === GAME_STATES.STATS_ALLOCATION && (
        <StatAllocation 
          onClose={() => setGameState(GAME_STATES.IDLE)} 
        />
      )}
      
      {userStats?.hasClass && userStats.unspentPoints > 0 && (
        <div style={{ 
          position: 'fixed', 
          bottom: '20px', 
          right: '20px', 
          zIndex: 1000 
        }}>
          <button 
            onClick={() => setGameState(GAME_STATES.STATS_ALLOCATION)}
            style={{
              backgroundColor: '#ff9800',
              color: 'white',
              border: 'none',
              borderRadius: '50%',
              width: '60px',
              height: '60px',
              fontSize: '24px',
              boxShadow: '0 3px 5px rgba(0,0,0,0.3)',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              border: '2px solid #f57800'
            }}
          >
            
          </button>
          <div style={{
            position: 'absolute',
            top: '-10px',
            right: '-10px',
            backgroundColor: '#e53935',
            color: 'white',
            borderRadius: '50%',
            width: '25px',
            height: '25px',
            fontSize: '14px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontWeight: 'bold'
          }}>
            {userStats.unspentPoints}
          </div>
        </div>
      )}
      
      {gameState === GAME_STATES.VICTORY && summary && (
        <div style={{ 
          marginBottom: '20px',
          padding: '25px',
          backgroundColor: '#3a1f6b',
          borderRadius: '12px',
          border: '2px solid #4caf50'
        }}>
          <div style={{ textAlign: 'center' }}>
            <div style={{ fontSize: '36px', marginBottom: '10px' }}></div>
            <h3 style={{ color: '#ffffff', marginBottom: '15px' }}></h3>
          </div>
          
          <div style={{ 
            backgroundColor: '#2c1810', 
            padding: '15px',
            borderRadius: '8px',
            color: '#e0e0e0'
          }}>
            <div style={{ marginBottom: '10px' }}>
              <span style={{ fontWeight: 'bold' }}></span> {summary.gainedExp}
            </div>
            <div style={{ marginBottom: '10px' }}>
              <span style={{ fontWeight: 'bold' }}></span> {summary.newLevel}
            </div>
            <div style={{ marginBottom: '10px' }}>
              <span style={{ fontWeight: 'bold' }}></span> {summary.unspentStatPoints || 0}
             </div>
            
            {summary.levelUp && (
              <div style={{ 
                color: '#4caf50', 
                fontWeight: 'bold',
                backgroundColor: '#1b5e20',
                padding: '10px',
                borderRadius: '6px',
                marginTop: '10px'
              }}>
                  +{summary.statPointsGained || 0} 
              </div>
            )}
          </div>
          
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <button 
              onClick={() => {
                setGameState(GAME_STATES.IDLE);
                setLogs([]);
              }}
              style={{
                padding: '10px 20px',
                backgroundColor: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: '6px',
                cursor: 'pointer',
                fontFamily: 'Courier New, monospace',
                fontWeight: 'bold',
                border: '2px solid #388e3c',
                transition: 'all 0.2s ease'
              }}
              onMouseOver={(e) => {
                e.target.style.backgroundColor = '#388e3c';
                e.target.style.transform = 'translateY(-1px)';
              }}
              onMouseOut={(e) => {
                e.target.style.backgroundColor = '#4caf50';
                e.target.style.transform = 'translateY(0)';
              }}
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default DungeonTest;