import { useState } from 'react';

/**
 * useApiAction
 *  API 
 * -  loading & error 
 * - 
 *
 * @param {Function} actionFn -  API  (args) => Promise
 * @param {Object} options
 * @param {Function} [options.onSuccess] -  ()
 * @param {Function} [options.onError] -  ()
 * @param {Function} [options.refresh] - 
 * @returns {{execute: ((function(...[*]): Promise<*|undefined>)|*), loading: boolean, error: string}}
 */
export function useApiAction(actionFn, { onSuccess, onError, refresh } = {}) {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const execute  = async (...params) => {
    setLoading(true);
    setError('');
    try {
      const result = await actionFn(...params);
      const input = params[0]; //   input 
      //  & 
      if (onSuccess) onSuccess(result, input); //   input
      if (refresh) await refresh();
      return result;
    } catch (err) {
      console.error(err);
      const msg = err.message || 'operation failed';
      setError(msg);
      if (onError) onError(msg);
      throw err;
    } finally {
      setLoading(false);
    }
  };

  return { execute, loading, error };
}

