import React, { createContext, useState, useEffect } from "react";
import axios from "axios";

// 
const AuthContext = createContext();

// 
export const AuthProvider = ({ children }) => {
  // 
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  // 
  useEffect(() => {
    const userInfo = localStorage.getItem("userInfo")
      ? JSON.parse(localStorage.getItem("userInfo"))
      : null;

    setUser(userInfo);
    setLoading(false);
  }, []);

  // 
  const initializeUserCards = async (token) => {
    try {
      // 
      const isNewRegistration = localStorage.getItem("isNewRegistration") === "true";
      
      // 
      if (isNewRegistration) {
        console.log("");
        
        try {
          // 
          const inventoryResponse = await axios.get("/api/cards/inventory?noAutoIssue=true", {
            headers: { Authorization: `Bearer ${token}` }
          });
          
          const cardData = inventoryResponse.data;
          console.log(":", cardData);
          
          // APIisNewRegistration=true
          const shortCardResponse = await axios.post("/api/cards/issue-daily?isNewRegistration=true", {}, {
            headers: { Authorization: `Bearer ${token}` }
          });
          console.log(":", shortCardResponse.data);
          
          // 
          const longCards = cardData.inventory?.filter(card => 
            card.type === "blank" && 
            card.taskDuration === "long" && 
            !card.used
          ) || [];
          
          // 
          if (longCards.length !== 3) {
            // 
            if (longCards.length > 0) {
              for (const card of longCards) {
                try {
                  await axios.delete(`/api/cards/${card._id}`, {
                    headers: { Authorization: `Bearer ${token}` }
                  });
                  console.log(`: ${card._id}`);
                } catch (error) {
                  console.error(`:`, error.response?.data?.message || error.message);
                }
              }
            }
            
            // 3
            for (let i = 0; i < 3; i++) {
              await axios.post("/api/cards/issue-blank", {
                title: "",
                description: "",
                taskDuration: "long"
              }, {
                headers: { Authorization: `Bearer ${token}` }
              });
            }
            console.log("3");
          } else {
            console.log("3");
          }
          
          // 
          const finalCheckResponse = await axios.get("/api/cards/inventory?noAutoIssue=true", {
            headers: { Authorization: `Bearer ${token}` }
          });
          
          const finalCardData = finalCheckResponse.data;
          const finalShortCards = finalCardData.inventory?.filter(card => card.taskDuration === "short" && card.type === "blank") || [];
          const finalLongCards = finalCardData.inventory?.filter(card => card.taskDuration === "long" && card.type === "blank") || [];
          
          console.log(`: ${finalShortCards.length}${finalLongCards.length}`);
          
          // 
          localStorage.removeItem("isNewRegistration");
          
          return; // 
        } catch (error) {
          console.error(":", error.response?.data?.message || error.message);
          // 
          localStorage.removeItem("isNewRegistration");
        }
      }
      
      //  ()
      // 
      const inventoryResponse = await axios.get("/api/cards/inventory?noAutoIssue=true", {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      const cardData = inventoryResponse.data;
      console.log(":", cardData);
      
      // 
      if (!cardData.inventory || cardData.inventory.length < 6) {
        console.log("...");
        
        // 
        const shortCards = cardData.inventory?.filter(card => card.taskDuration === "short" && card.type === "blank") || [];
        const longCards = cardData.inventory?.filter(card => card.taskDuration === "long" && card.type === "blank") || [];
        
        let madeChanges = false;
        
        // 3
        if (shortCards.length < 3) {
          const shortCardsNeeded = 3 - shortCards.length;
          console.log(`${shortCardsNeeded}`);
          
          try {
            // 
            for (let i = 0; i < shortCardsNeeded; i++) {
              await axios.post("/api/cards/issue-blank", {
                title: "",
                description: "",
                taskDuration: "short"
              }, {
                headers: { Authorization: `Bearer ${token}` }
              });
            }
            console.log(`${shortCardsNeeded}`);
            madeChanges = true;
          } catch (err) {
            console.error(":", err.response?.data?.message || err.message);
          }
        } else if (shortCards.length > 3) {
          console.log(`(${shortCards.length})3`);
          
          try {
            // 
            const sortedShortCards = [...shortCards].sort((a, b) => {
              const dateA = new Date(a.issuedAt || a.createdAt);
              const dateB = new Date(b.issuedAt || b.createdAt);
              return dateB - dateA; // 
            });
            
            // 3
            const cardsToKeep = sortedShortCards.slice(0, 3);
            const cardsToRemove = sortedShortCards.slice(3);
            
            console.log(`${cardsToKeep.length}${cardsToRemove.length}`);
            
            // 
            for (const card of cardsToRemove) {
              try {
                await axios.delete(`/api/cards/${card._id}`, {
                  headers: { Authorization: `Bearer ${token}` }
                });
                console.log(`: ${card._id}`);
              } catch (error) {
                console.error(`:`, error.response?.data?.message || error.message);
              }
            }
            madeChanges = true;
          } catch (error) {
            console.error(":", error);
          }
        } else {
          console.log("3");
        }
        
        // 3
        if (longCards.length < 3) {
          const longCardsNeeded = 3 - longCards.length;
          console.log(`${longCardsNeeded}`);
          
          try {
            // 
            for (let i = 0; i < longCardsNeeded; i++) {
              await axios.post("/api/cards/issue-blank", {
                title: "",
                description: "",
                taskDuration: "long"
              }, {
                headers: { Authorization: `Bearer ${token}` }
              });
            }
            console.log(`${longCardsNeeded}`);
            madeChanges = true;
          } catch (error) {
            console.error(":", error.response?.data?.message || error.message);
          }
        } else if (longCards.length > 3) {
          console.log(`(${longCards.length})3`);
          
          try {
            // 
            const sortedLongCards = [...longCards].sort((a, b) => {
              const dateA = new Date(a.issuedAt || a.createdAt);
              const dateB = new Date(b.issuedAt || b.createdAt);
              return dateB - dateA; // 
            });
            
            // 3
            const cardsToKeep = sortedLongCards.slice(0, 3);
            const cardsToRemove = sortedLongCards.slice(3);
            
            console.log(`${cardsToKeep.length}${cardsToRemove.length}`);
            
            // 
            for (const card of cardsToRemove) {
              try {
                await axios.delete(`/api/cards/${card._id}`, {
                  headers: { Authorization: `Bearer ${token}` }
                });
                console.log(`: ${card._id}`);
              } catch (error) {
                console.error(`:`, error.response?.data?.message || error.message);
              }
            }
            madeChanges = true;
          } catch (error) {
            console.error(":", error);
          }
        } else {
          console.log("3");
        }
        
        // 
        if (madeChanges) {
          // 
          const finalCheckResponse = await axios.get("/api/cards/inventory?noAutoIssue=true", {
            headers: { Authorization: `Bearer ${token}` }
          });
          
          const finalCardData = finalCheckResponse.data;
          const finalShortCards = finalCardData.inventory?.filter(card => card.taskDuration === "short" && card.type === "blank") || [];
          const finalLongCards = finalCardData.inventory?.filter(card => card.taskDuration === "long" && card.type === "blank") || [];
          
          console.log(`: ${finalShortCards.length}${finalLongCards.length}`);
        }
      } else {
        console.log("");
      }
    } catch (error) {
      console.error(":", error.response?.data?.message || error.message);
    }
  };

  // 
  const login = async (email, password) => {
    try {
      setLoading(true);
      setError(null);
      const { data } = await axios.post("/api/users/login", {
        email,
        password,
      });
      // 
      localStorage.setItem("userInfo", JSON.stringify(data));
      setUser(data);
      
      // 
      await initializeUserCards(data.token);
      
      return data;
    } catch (error) {
      setError(
        error.response && error.response.data.message
          ? error.response.data.message
          : "Failed to login, please check your credentials"
      );
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // 
  const register = async (username, email, password) => {
    try {
      setLoading(true);
      setError(null);

      const { data } = await axios.post("/api/users/register", {
        username,
        email,
        password,
      });

      // 
      localStorage.setItem("userInfo", JSON.stringify(data));
      setUser(data);
      
      // 
      console.log("...");
      
      // localStorage
      localStorage.setItem("isNewRegistration", "true");
      
      await initializeUserCards(data.token);

      return data;
    } catch (error) {
      setError(
        error.response && error.response.data.message
          ? error.response.data.message
          : "Failed to register, please check your credentials"
      );
      throw error;
    } finally {
      setLoading(false);
    }
  };

  // 
  const logout = () => {
    localStorage.removeItem("userInfo");
    setUser(null);
  };

  // 
  const updateProfile = async (userData) => {
    try {
      setLoading(true);
      setError(null);

      // token
      const config = {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      };

      const { data } = await axios.put("/api/users/profile", userData, config);

      // 
      const updatedUser = { ...user, ...data };
      localStorage.setItem("userInfo", JSON.stringify(updatedUser));
      setUser(updatedUser);

      return data;
    } catch (error) {
      setError(
        error.response && error.response.data.message
          ? error.response.data.message
          : "Failed to update profile, please try again"
      );
      throw error;
    } finally {
      setLoading(false);
    }
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        loading,
        error,
        login,
        register,
        logout,
        updateProfile,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default AuthContext;
