import React, { useState, useEffect, useRef } from 'react';

// 
const CLASS_COMBAT_CONFIG = {
  warrior: {
    name: '',
    getDamage: (playerStats) => {
      return Math.floor((playerStats.attack || 10) * (0.7 + Math.random() * 0.2));
    },
    reduceDamage: (damage, playerStats) => {
      const defenseReduction = Math.floor((playerStats.defense || 5) * 0.35);
      return Math.max(1, damage - defenseReduction);
    },
    getCritChance: (playerStats) => {
      return playerStats.critRate || 5;
    }
  },
  mage: {
    name: '',
    getDamage: (playerStats) => {
      return Math.floor((playerStats.magicPower || 10) * (0.9 + Math.random() * 0.6));
    },
    reduceDamage: (damage, playerStats) => {
      const defenseReduction = Math.floor((playerStats.defense || 5) * 0.2);
      return Math.max(1, damage - defenseReduction);
    },
    getCritChance: (playerStats) => {
      return (playerStats.critRate || 5) + 5; // 
    }
  },
  rogue: {
    name: '',
    getDamage: (playerStats) => {
      const speedBonus = Math.floor((playerStats.speed || 5) * 0.2);
      return Math.floor((playerStats.attack || 10) * 0.6 + speedBonus + (Math.random() * 5));
    },
    reduceDamage: (damage, playerStats) => {
      const defenseReduction = Math.floor((playerStats.defense || 5) * 0.25);
      return Math.max(1, damage - defenseReduction);
    },
    getCritChance: (playerStats) => {
      return (playerStats.critRate || 5) + 10;
    },
    getEvasionBonus: (playerStats) => {
      return (playerStats.speed || 5) * 0.5;
    }
  },
  archer: {
    name: '',
    getDamage: (playerStats) => {
      const critBonus = Math.floor((playerStats.critRate || 5) * 0.3);
      return Math.floor((playerStats.attack || 10) * 0.7 + critBonus + (Math.random() * 3));
    },
    reduceDamage: (damage, playerStats) => {
      const defenseReduction = Math.floor((playerStats.defense || 5) * 0.25);
      return Math.max(1, damage - defenseReduction);
    },
    getCritChance: (playerStats, isFirstAttack) => {
      return (playerStats.critRate || 5) + (isFirstAttack ? 20 : 0);
    }
  }
};

// slug
const getClassSlugFromName = (className) => {
  if (!className) return 'warrior';
  const name = className.toLowerCase();
  if (name === 'mage') return 'mage';
  if (name === 'rogue') return 'rogue';
  if (name === 'archer') return 'archer';
  return 'warrior';
};

const CombatSystem = ({ 
  monsters, 
  playerStats, 
  playerClass = "warrior", 
  playerClassName,
  onCombatEnd,
  skills = [],
  userToken // tokenAPI
}) => {
  // classNameclassSlug
  console.log('=== CombatSystem  ===');
  console.log('playerStats:', playerStats);
  console.log('playerClass:', playerClass);
  console.log('magicPower:', playerStats.magicPower);
  console.log('baseStats:', playerStats.baseStats);
  const actualPlayerClass = playerClass || getClassSlugFromName(playerClassName);
  
  const [currentMonsterIndex, setCurrentMonsterIndex] = useState(0);
  const [playerHp, setPlayerHp] = useState(playerStats.hp);
  const [monsterHp, setMonsterHp] = useState(monsters[0]?.stats?.hp || 100);
  const [combatLogs, setCombatLogs] = useState([]);
  const [isAttacking, setIsAttacking] = useState(false);
  const [isPlayerTurn, setIsPlayerTurn] = useState(true);
  const [combatEnded, setCombatEnded] = useState(false);
  const [showDamage, setShowDamage] = useState(null);
  const [isFirstAttack, setIsFirstAttack] = useState(true);
  const [currentTurn, setCurrentTurn] = useState(0);
  const maxPlayerHpRef = useRef(playerStats.hp);
  const [maxMonsterHp, setMaxMonsterHp] = useState(
    monsters[0]?.stats?.hp ?? 100
  );
 
  
  // 
  const [activeSkills, setActiveSkills] = useState(skills || []);
  const [skillCooldowns, setSkillCooldowns] = useState({});
  const [monsterStatuses, setMonsterStatuses] = useState({});
  const [playerStatuses, setPlayerStatuses] = useState({});
  const [skillTriggeredEffects, setSkillTriggeredEffects] = useState([]);
  
  // 
  const [dropResults, setDropResults] = useState(null);
  const [showDropAnimation, setShowDropAnimation] = useState(false);
  const [isProcessingDrops, setIsProcessingDrops] = useState(false);
  
  const currentMonster = monsters[currentMonsterIndex];
  const logsEndRef = useRef(null);

  // 
  const classConfig = CLASS_COMBAT_CONFIG[actualPlayerClass] || CLASS_COMBAT_CONFIG.warrior;
  
  // 
  useEffect(() => {
  const hp = currentMonster?.stats?.hp ?? 100;
   setMonsterHp(hp);
    setMaxMonsterHp(hp);
  }, [currentMonster]);
  
  useEffect(() => {
    // 
    const initialCooldowns = {};
    activeSkills.forEach(skill => {
      initialCooldowns[skill.id || skill._id] = 0;
    });
    setSkillCooldowns(initialCooldowns);
    
    // 
    setCombatLogs([
      `! : ${classConfig.name}`,
      `: ${activeSkills.map(s => s.name).join(', ') || ''}`
    ]);
    
    // 
    triggerSkills('onStartBattle', null);
  }, [activeSkills, classConfig.name]);
  
  // 
  useEffect(() => {
    if (logsEndRef.current) {
      logsEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [combatLogs]);
  
  // 
  const triggerSkills = (triggerType, context) => {
    // 
    const triggeredSkills = activeSkills.filter(skill => {
      // 
      if (skill.trigger !== triggerType) return false;
      
      // 
      const skillId = skill.id || skill._id;
      if (skillCooldowns[skillId] > 0) return false;
      
      // 
      if (skill.once && playerStatuses[`used_${skillId}`]) return false;
      
      // 
      if (triggerType === 'onHpBelow' && skill.triggerCondition?.hpBelow) {
        const hpPercentage = playerHp / playerStats.hp;
        if (hpPercentage > skill.triggerCondition.hpBelow) return false;
      }
      
      return true;
    });
    
    // 
    triggeredSkills.sort((a, b) => (b.priority || 0) - (a.priority || 0));
    
    // 
    const effects = triggeredSkills.map(skill => {
      const skillId = skill.id || skill._id;
      
      // 
      setSkillCooldowns(prev => ({ 
        ...prev, 
        [skillId]: skill.cooldown || 0 
      }));
      
      // 
      if (skill.once) {
        setPlayerStatuses(prev => ({ 
          ...prev, 
          [`used_${skillId}`]: true 
        }));
      }
      
      // 
      const effectObj = {
        name: skill.name,
        effect: skill.effect,
        value: skill.effectValue,
        source: 'player',
        skillId
      };
      
      // 
      let logMessage = '';
      switch (skill.effect) {
        case 'dealDamage':
          logMessage = `  "${skill.name}" ${skill.effectValue} `;
          break;
        case 'gainShield':
          logMessage = `  "${skill.name}" ${skill.effectValue} `;
          break;
        case 'heal':
          logMessage = `  "${skill.name}" ${skill.effectValue} `;
          break;
        case 'buffAttack':
          logMessage = `  "${skill.name}" ${skill.effectValue}`;
          break;
        case 'debuffEnemy':
          logMessage = `  "${skill.name}" ${skill.effectValue}`;
          break;
        default:
          logMessage = `  "${skill.name}"`;
      }
      
      setCombatLogs(prev => [...prev, logMessage]);
      
      // 
      if (skill.triggerCondition?.applyStatus) {
        const statusType = skill.triggerCondition.applyStatus;
        setMonsterStatuses(prev => ({
          ...prev,
          [statusType]: {
            duration: 3, // 3
            source: 'player'
          }
        }));
        
        // 
        let statusMessage = '';
        switch (statusType) {
          case 'bleed':
            statusMessage = ` ${currentMonster.name} `;
            break;
          case 'poison':
            statusMessage = ` ${currentMonster.name} `;
            break;
          case 'confusion':
            statusMessage = ` ${currentMonster.name} `;
            break;
          default:
            statusMessage = ` ${currentMonster.name}  ${statusType} `;
        }
        
        setCombatLogs(prev => [...prev, statusMessage]);
      }
      
      return effectObj;
    });
    
    setSkillTriggeredEffects(effects);
    return effects;
  };
  
  // 
  const applySkillEffects = (effects, target) => {
    effects.forEach(effect => {
      switch (effect.effect) {
        case 'dealDamage':
          if (target === 'monster') {
            setMonsterHp(prev => Math.max(0, prev - effect.value));
          } else {
            setPlayerHp(prev => Math.max(0, prev - effect.value));
          }
          break;
        case 'gainShield':
          //  - HP
          setPlayerHp(prev => Math.min(playerStats.hp, prev + effect.value));
          break;
        case 'heal':
          setPlayerHp(prev => Math.min(playerStats.hp, prev + effect.value));
          break;
        // ...
      }
    });
  };
  
  // 
  const processStatusEffects = () => {
    // 
    Object.entries(monsterStatuses).forEach(([statusType, status]) => {
      if (status.duration <= 0) {
        // 
        setMonsterStatuses(prev => {
          const newStatuses = {...prev};
          delete newStatuses[statusType];
          return newStatuses;
        });
        return;
      }
      
      // 
      switch (statusType) {
        case 'bleed':
          const bleedDamage = Math.floor(5 + (currentTurn * 1.5)); // 
          setMonsterHp(prev => Math.max(0, prev - bleedDamage));
          setCombatLogs(prev => [...prev, ` : ${currentMonster.name}  ${bleedDamage} `]);
          break;
        case 'poison':
          const poisonDamage = 8; // 
          setMonsterHp(prev => Math.max(0, prev - poisonDamage));
          setCombatLogs(prev => [...prev, ` : ${currentMonster.name}  ${poisonDamage} `]);
          break;
      }
      
      // 
      setMonsterStatuses(prev => ({
        ...prev,
        [statusType]: {
          ...status,
          duration: status.duration - 1
        }
      }));
    });
  };
  
  // 
  const updateCooldowns = () => {
    setSkillCooldowns(prev => {
      const newCooldowns = {...prev};
      
      Object.keys(newCooldowns).forEach(skillId => {
        if (newCooldowns[skillId] > 0) {
          newCooldowns[skillId]--;
        }
      });
      
      return newCooldowns;
    });
  };
  
  // 
  const handleDropProcessing = async () => {
    if (isProcessingDrops) return; // 
    
    try {
      setIsProcessingDrops(true);
      
      // 
      setCombatLogs(prev => [...prev, ' ...']);
      
      // ID
      const monsterIds = monsters.map(monster => {
        //  _id id
        const id = monster._id || monster.id;
        
        // ID
        if (id && typeof id === 'object' && id.$oid) {
          // ObjectId
          return id.$oid;
        }
        
        return typeof id === 'object' ? String(id) : id;
      }).filter(id => id); // ID

      console.log('=== FRONTEND DROP DEBUG ===');
      console.log(':', monsters);
      console.log('ID:', monsterIds);
      console.log('ID:', monsterIds.length);
      console.log(':', monsters.length);
      
      // ID
      const invalidIds = monsterIds.filter(id => {
        // ObjectId24hex
        return !id || typeof id !== 'string' || !/^[0-9a-fA-F]{24}$/.test(id);
      });
      
      if (invalidIds.length > 0) {
        console.error('ID:', invalidIds);
        throw new Error(` ${invalidIds.length} ID`);
      }
      
      if (monsterIds.length !== monsters.length) {
        console.warn(`ID(${monsterIds.length})(${monsters.length})`);
        const missingIds = monsters.filter(monster => !monster._id && !monster.id);
        console.error('ID:', missingIds);
        throw new Error('ID');
      }
      
      console.log('payload:', JSON.stringify({ monsterIds }, null, 2));
      
      const response = await fetch('/api/cards/process-drops', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${userToken}`
        },
        body: JSON.stringify({
          monsterIds: monsterIds
        })
      });
      
      if (!response.ok) {
        // 
        let errorMessage = `: ${response.statusText}`;
        try {
          const errorData = await response.json();
          if (errorData.error) {
            errorMessage = `: ${errorData.error}`;
            if (errorData.details) {
              console.error(':', errorData.details);
            }
          }
        } catch (e) {
          console.error(':', e);
        }
        throw new Error(errorMessage);
      }
      
      const dropData = await response.json();
      
      if (dropData.success) {
        setDropResults(dropData.data);
        
        // in logs
        const logs = [];
        if (dropData.data.gold > 0) {
          logs.push(`  ${dropData.data.gold} `);
        }
        if (dropData.data.exp > 0) {
          logs.push(`  ${dropData.data.exp} `);
        }
        if (dropData.data.items && dropData.data.items.length > 0) {
          logs.push(` : ${dropData.data.items.map(item => item.name).join(', ')}`);
        }
        if (dropData.data.cards && dropData.data.cards.length > 0) {
          logs.push(` : ${dropData.data.cards.map(card => card.title).join(', ')}`);
        }
        
        setCombatLogs(prev => [...prev, ...logs]);
        
        // 
        setShowDropAnimation(true);
        
        // 3
        setTimeout(() => {
          setShowDropAnimation(false);
          // 
          onCombatEnd({ 
            result: 'victory', 
            remainingHp: playerHp,
            drops: dropData.data
          });
        }, 3000);
      } else {
        throw new Error(dropData.message || '');
      }
    } catch (error) {
      console.error(':', error);
      console.error(':', {
        message: error.message,
        stack: error.stack,
        monsters: monsters,
        monstersWithIds: monsters.map(m => ({ name: m.name, _id: m._id, id: m.id }))
      });
      
      setCombatLogs(prev => [...prev, ` : ${error.message}`]);
      
      // 
      setTimeout(() => {
        onCombatEnd({ 
          result: 'victory', 
          remainingHp: playerHp
        });
      }, 1500);
    } finally {
      setIsProcessingDrops(false);
    }
  };
  // 
  useEffect(() => {
    if (skills && skills.length > 0) {
      const triggerTypes = {};
      skills.forEach(skill => {
        const trigger = skill.trigger || "";
        triggerTypes[trigger] = (triggerTypes[trigger] || 0) + 1;
      });
    }
    
    if (combatEnded) return;
    
    // 
    if (isPlayerTurn) {
      const timer = setTimeout(() => {
        // 
        setCurrentTurn(prev => prev + 1);
        
        // 
        processStatusEffects();
        
        // 
        const baseDamage = classConfig.getDamage(playerStats);
        
        // 
        const critChance = typeof classConfig.getCritChance === 'function' 
          ? classConfig.getCritChance(playerStats, isFirstAttack)
          : (playerStats.critRate || 5);
        
        const isCritical = Math.random() * 100 < critChance;
        let damage = baseDamage;
        
        if (isCritical) {
          damage = Math.floor(damage * 1.5);
        }
        
        // 
        const attackSkillEffects = triggerSkills('onAttack', { isCritical });
        
        // 
        let skillDamage = 0;
        attackSkillEffects.forEach(effect => {
          if (effect.effect === 'dealDamage') {
            skillDamage += effect.value;
          }
        });
        
        // 
        const totalDamage = damage + skillDamage;
        
        setShowDamage({ target: 'monster', value: totalDamage });
        setIsAttacking(true);
        
        setTimeout(() => {
          setIsAttacking(false);
          setShowDamage(null);
          
          const newMonsterHp = Math.max(0, monsterHp - totalDamage);
          setMonsterHp(newMonsterHp);
          
          // 
          let logMessage = `  ${currentMonster.name} ${totalDamage} `;
          if (isCritical) {
            logMessage = `CRITICAL! ${logMessage}`;
          }
          
          // 
          if (skillDamage > 0) {
            logMessage += ` (: ${skillDamage}, : ${damage})`;
          }
          
          setCombatLogs(prev => [...prev, logMessage]);
          
          // 
          if (newMonsterHp <= 0) {
            setCombatLogs(prev => [...prev, `  ${currentMonster.name}`]);
            
            // 
            if (currentMonsterIndex >= monsters.length - 1) {
              // 
              setCombatEnded(true);
              handleDropProcessing();
            } else {
              // 
              setCurrentMonsterIndex(prev => prev + 1);
              setMonsterHp(monsters[currentMonsterIndex + 1]?.stats?.hp|| 100);
              
              setIsPlayerTurn(true); // 
              setIsFirstAttack(true); // 
              
              // 
              setMonsterStatuses({});
            }
          } else {
            setIsFirstAttack(false); // 
            setIsPlayerTurn(false);
            
            // 
            updateCooldowns();
          }
        }, 600);
      }, 1000);
      
      return () => clearTimeout(timer);
    } 
    // 
    else {
      const timer = setTimeout(() => {
        // 
        const baseEvasion = playerStats.evasion || 0;
        const bonusEvasion = actualPlayerClass === 'rogue' && classConfig.getEvasionBonus 
          ? classConfig.getEvasionBonus(playerStats) 
          : 0;
        
        const totalEvasion = baseEvasion + bonusEvasion;
        const isEvaded = Math.random() * 100 < totalEvasion;
        
        if (isEvaded) {
          // 
          setCombatLogs(prev => [...prev, `EVADE!  ${currentMonster.name} `]);
          
          // 
          const evadeSkillEffects = triggerSkills('onReceiveHit', { evaded: true });
          
          setIsPlayerTurn(true);
        } else {
          // 
          const monsterDamage = Math.floor(((currentMonster.stats.attack || 8) * (0.7 + Math.random() * 0.5)));
          
          // 
          const hitSkillEffects = triggerSkills('onReceiveHit', { evaded: false });
          
          // 
          const reducedDamage = classConfig.reduceDamage(monsterDamage, playerStats);
          setShowDamage({ target: 'player', value: reducedDamage });
          
          setTimeout(() => {
            setShowDamage(null);
            
            // 
            applySkillEffects(hitSkillEffects, 'player');
            
            const newPlayerHp = Math.max(0, playerHp - reducedDamage);
            setPlayerHp(newPlayerHp);
            
            // 
            const damageReduction = monsterDamage - reducedDamage;
            let logMessage = ` ${currentMonster.name}  ${reducedDamage} `;
            
            setCombatLogs(prev => [...prev, logMessage]);
            
            if (damageReduction > 0) {
              setCombatLogs(prev => [...prev, `  ${damageReduction} `]);
            }
            
            // 
            if (newPlayerHp < playerStats.hp * 0.5) {
              const lowHpSkillEffects = triggerSkills('onHpBelow', { 
                hpPercentage: newPlayerHp / playerStats.hp 
              });
              
              // 
              applySkillEffects(lowHpSkillEffects, 'player');
            }
            
            //  (HP0)
            if (newPlayerHp <= 0) {
              setCombatLogs(prev => [...prev, `  ${currentMonster.name} `]);
              setCombatEnded(true);
              setTimeout(() => {
                // GameOver
                onCombatEnd({ 
                  result: 'settlement', 
                  remainingHp: 0
                });
              }, 1500);
            } else {
              setIsPlayerTurn(true);
            }
          }, 600);
        }
      }, 1500);
      
      return () => clearTimeout(timer);
    }
  }, [
    isPlayerTurn, 
    currentMonsterIndex, 
    combatEnded, 
    currentMonster, 
    monsterHp, 
    playerHp, 
    playerStats, 
    onCombatEnd, 
    classConfig, 
    actualPlayerClass, 
    isFirstAttack,
    currentTurn,
    monsterStatuses,
    isProcessingDrops
  ]);
  
  // UI
  const renderSkillsUI = () => {
    if (!activeSkills || activeSkills.length === 0) return null;
    
    return (
      <div style={{
        marginTop: '15px',
        padding: '12px',
        backgroundColor: '#4c2a85',
        borderRadius: '8px',
        fontSize: '13px',
        border: '2px solid #5d3494',
        color: '#e0e0e0'
      }}>
        <div style={{ 
          fontWeight: 'bold', 
          marginBottom: '8px',
          color: '#ffffff',
          textAlign: 'center'
        }}>
           
        </div>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '8px' }}>
          {activeSkills.map(skill => {
            const skillId = skill.id || skill._id;
            const isOnCooldown = skillCooldowns[skillId] > 0;
            
            return (
              <div key={skillId} style={{
                padding: '6px 10px',
                backgroundColor: isOnCooldown ? '#2c1810' : '#3a1f6b',
                border: `2px solid ${isOnCooldown ? '#666' : '#7e4ab8'}`,
                borderRadius: '6px',
                opacity: isOnCooldown ? 0.6 : 1,
                color: '#e0e0e0'
              }}>
                <span style={{ fontWeight: 'bold' }}>{skill.name}</span>
                {isOnCooldown && (
                  <span style={{ color: '#b89be6' }}> (CD: {skillCooldowns[skillId]})</span>
                )}
              </div>
            );
          })}
        </div>
      </div>
    );
  };
  // 
  const DropAnimation = () => {
    if (!showDropAnimation || !dropResults) return null;
    
    return (
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(44, 24, 16, 0.95)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1000,
        animation: 'fadeIn 0.5s ease-in',
        fontFamily: 'Courier New, monospace'
      }}>
        <div style={{
          backgroundColor: '#3a1f6b',
          borderRadius: '12px',
          padding: '30px',
          border: '3px solid #5d3494',
          maxWidth: '500px',
          width: '90%',
          textAlign: 'center',
          color: '#e0e0e0'
        }}>
          <h2 style={{ 
            color: '#ffa726',
            marginBottom: '20px',
            textShadow: '2px 2px 0px #2c1810'
          }}>
             
          </h2>
          
          {/*  */}
          {(dropResults.gold > 0 || dropResults.exp > 0) && (
            <div style={{
              display: 'flex',
              justifyContent: 'center',
              gap: '30px',
              marginBottom: '20px'
            }}>
              {dropResults.gold > 0 && (
                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  fontSize: '24px',
                  color: '#ffa726',
                  backgroundColor: '#2c1810',
                  padding: '10px 15px',
                  borderRadius: '8px',
                  border: '2px solid #ffa726'
                }}>
                  <span style={{ fontSize: '36px', marginRight: '10px' }}></span>
                  +{dropResults.gold}
                </div>
              )}
              {dropResults.exp > 0 && (
                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  fontSize: '24px',
                  color: '#81c784',
                  backgroundColor: '#2c1810',
                  padding: '10px 15px',
                  borderRadius: '8px',
                  border: '2px solid #81c784'
                }}>
                  <span style={{ fontSize: '36px', marginRight: '10px' }}></span>
                  +{dropResults.exp}
                </div>
              )}
            </div>
          )}
          
          {/*  */}
          {dropResults.items && dropResults.items.length > 0 && (
            <div style={{ marginBottom: '20px' }}>
              <h3 style={{ color: '#ffffff', marginBottom: '10px' }}></h3>
              <div style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(120px, 1fr))',
                gap: '10px'
              }}>
                {dropResults.items.map((item, index) => (
                  <div key={index} style={{
                    backgroundColor: '#2c1810',
                    border: '2px solid #4caf50',
                    borderRadius: '8px',
                    padding: '10px',
                    animation: `bounceIn 0.6s ease-out ${index * 0.2}s both`
                  }}>
                    <div style={{ fontSize: '24px', marginBottom: '5px' }}></div>
                    <div style={{ fontSize: '14px', fontWeight: 'bold', color: '#ffffff' }}>
                      {item.name}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
          
          {/*  */}
          {dropResults.cards && dropResults.cards.length > 0 && (
            <div style={{ marginBottom: '20px' }}>
              <h3 style={{ color: '#ffffff', marginBottom: '10px' }}></h3>
              <div style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))',
                gap: '10px'
              }}>
                {dropResults.cards.map((card, index) => (
                  <div key={index} style={{
                    backgroundColor: '#2c1810',
                    border: '2px solid #9c27b0',
                    borderRadius: '8px',
                    padding: '10px',
                    animation: `bounceIn 0.6s ease-out ${index * 0.2}s both`
                  }}>
                    <div style={{ fontSize: '24px', marginBottom: '5px' }}></div>
                    <div style={{ fontSize: '14px', fontWeight: 'bold', color: '#ffffff' }}>
                      {card.title}
                    </div>
                    <div style={{ fontSize: '12px', color: '#b89be6', marginTop: '5px' }}>
                      {card.bonus && card.bonus.experienceMultiplier > 1 && ` +${Math.round((card.bonus.experienceMultiplier - 1) * 100)}%`}
                      {card.bonus && card.bonus.goldMultiplier > 1 && `  +${Math.round((card.bonus.goldMultiplier - 1) * 100)}%`}
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
          
          <div style={{
            marginTop: '20px',
            fontSize: '16px',
            color: '#b89be6'
          }}>
            3...
          </div>
        </div>
      </div>
    );
  };
  
  return (
    <div className="combat-container" style={{
      border: '3px solid #5d3494',
      borderRadius: '12px',
      padding: '20px',
      backgroundColor: '#3a1f6b',
      maxWidth: '700px',
      margin: '0 auto',
      fontFamily: 'Courier New, monospace',
      color: '#e0e0e0'
    }}>
      <h3 style={{ 
        textAlign: 'center', 
        marginTop: 0,
        color: '#ffffff',
        textShadow: '1px 1px 0px #2c1810'
      }}>
          {currentMonsterIndex + 1}/{monsters.length} 
        <span style={{ marginLeft: '10px', fontSize: '0.8em', color: '#b89be6' }}>
          ({classConfig.name})
        </span>
      </h3>
      
      <div className="combat-arena" style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '20px',
        position: 'relative',
        minHeight: '180px',
        backgroundColor: '#2c1810',
        borderRadius: '8px',
        marginBottom: '15px',
        border: '2px solid #4c2a85'
      }}>
        {/*  */}
        <div className={`player ${isAttacking && isPlayerTurn ? 'attacking' : ''}`} style={{
          textAlign: 'center',
          position: 'relative',
          transform: isAttacking && isPlayerTurn ? 'translateX(20px)' : 'translateX(0)',
          transition: 'transform 0.2s ease-in-out'
        }}>
          <div style={{ 
            width: '80px', 
            height: '100px', 
            backgroundColor: actualPlayerClass === 'warrior' ? '#4c6ef5' : 
                             actualPlayerClass === 'mage' ? '#9c27b0' : 
                             actualPlayerClass === 'rogue' ? '#546e7a' : 
                             actualPlayerClass === 'archer' ? '#2e7d32' : '#4c6ef5',
            borderRadius: '8px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontSize: '24px',
            color: 'white',
            margin: '0 auto',
            border: '2px solid #7e4ab8'
          }}>
            {actualPlayerClass === 'warrior' ? '' : 
             actualPlayerClass === 'mage' ? '' : 
             actualPlayerClass === 'rogue' ? '' : 
             actualPlayerClass === 'archer' ? '' : ''}
          </div>
          <div style={{ marginTop: '10px' }}>
            <div style={{ fontWeight: 'bold', color: '#ffffff' }}> ({classConfig.name})</div>
            <div className="health-bar" style={{
              width: '120px',
              height: '12px',
              backgroundColor: '#d32f2f',
              borderRadius: '6px',
              overflow: 'hidden',
              marginTop: '5px',
              border: '1px solid #7e4ab8'
            }}>
              <div style={{
                width: `${(playerHp / maxPlayerHpRef.current) * 100}%`,
                height: '100%',
                backgroundColor: '#4caf50',
                transition: 'width 0.5s ease-out'
              }}></div>
            </div>
            <div style={{ fontSize: '12px', marginTop: '3px', color: '#b89be6' }}>
              HP: {playerHp}/{maxPlayerHpRef.current}
            </div>
          </div>
          
          {showDamage && showDamage.target === 'player' && (
            <div className="damage-indicator" style={{
              position: 'absolute',
              top: '-20px',
              left: '50%',
              transform: 'translateX(-50%)',
              color: '#f44336',
              fontWeight: 'bold',
              fontSize: '20px',
              animation: 'damage-float 0.8s ease-out',
              textShadow: '2px 2px 0px #2c1810'
            }}>
              -{showDamage.value}
            </div>
          )}
          
          {/*  */}
          {Object.keys(playerStatuses).length > 0 && (
            <div style={{
              position: 'absolute',
              bottom: '-25px',
              left: '50%',
              transform: 'translateX(-50%)',
              display: 'flex',
              gap: '5px'
            }}>
              {Object.entries(playerStatuses).map(([status, data]) => {
                // 
                if (status.startsWith('used_')) return null;
                
                let icon = '';
                if (status === 'bleed') icon = '';
                if (status === 'poison') icon = '';
                if (status === 'confusion') icon = '';
                
                return (
                  <div key={status} style={{
                    width: '20px',
                    height: '20px',
                    borderRadius: '50%',
                    backgroundColor: '#4c2a85',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    fontSize: '12px',
                    border: '1px solid #7e4ab8'
                  }}>
                    {icon}
                  </div>
                );
              })}
            </div>
          )}
        </div>
        
        {/* VS */}
        <div style={{
          fontSize: '24px',
          fontWeight: 'bold',
          color: '#b89be6'
        }}>
          VS
        </div>
        
        {/*  */}
        <div className={`monster ${isAttacking && !isPlayerTurn ? 'attacking' : ''}`} style={{
          textAlign: 'center',
          position: 'relative',
          transform: isAttacking && !isPlayerTurn ? 'translateX(-20px)' : 'translateX(0)',
          transition: 'transform 0.2s ease-in-out'
        }}>
          {/*  */}
          <div style={{
            width: '90px',
            height: '110px',
            backgroundColor: currentMonster.type === 'boss' ? '#d32f2f' : '#5d3494',
            borderRadius: '8px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            margin: '0 auto',
            border: '2px solid #7e4ab8',
            overflow: 'hidden'
          }}>
            {currentMonster.icon ? (
              <img
                src={`/Icon/Monster/${currentMonster.icon}.png`}
                alt={currentMonster.name}
                style={{ maxWidth: '100%', maxHeight: '100%' }}
              />
            ) : (
              currentMonster.type === 'boss' ? '' : ''
            )}
          </div>

          <div style={{ marginTop: '10px' }}>
            <div style={{ fontWeight: 'bold', color: '#ffffff' }}>
              {currentMonster.name} {currentMonster.type === 'boss' && '(BOSS)'}
            </div>
            <div className="health-bar" style={{
              width: '120px',
              height: '12px',
              backgroundColor: '#d32f2f',
              borderRadius: '6px',
              overflow: 'hidden',
              marginTop: '5px',
              border: '1px solid #7e4ab8'
            }}>
              <div style={{
                width: `${(monsterHp / maxMonsterHp) * 100}%`,
                height: '100%',
                backgroundColor: '#ff9800',
                transition: 'width 0.5s ease-out'
              }}></div>
            </div>
            <div style={{ fontSize: '12px', marginTop: '3px', color: '#b89be6' }}>
              HP: {monsterHp}/{maxMonsterHp}
            </div>
          </div>
          
          {showDamage && showDamage.target === 'monster' && (
            <div className="damage-indicator" style={{
              position: 'absolute',
              top: '-20px',
              left: '50%',
              transform: 'translateX(-50%)',
              color: '#f44336',
              fontWeight: 'bold',
              fontSize: '20px',
              animation: 'damage-float 0.8s ease-out',
              textShadow: '2px 2px 0px #2c1810'
            }}>
              -{showDamage.value}
            </div>
          )}
          
          {/*  */}
          {Object.keys(monsterStatuses).length > 0 && (
            <div style={{
              position: 'absolute',
              bottom: '-25px',
              left: '50%',
              transform: 'translateX(-50%)',
              display: 'flex',
              gap: '5px'
            }}>
              {Object.entries(monsterStatuses).map(([status, data]) => {
                let icon = '';
                let bgColor = '#4c2a85';
                
                if (status === 'bleed') {
                  icon = '';
                  bgColor = '#d32f2f';
                }
                if (status === 'poison') {
                  icon = '';
                  bgColor = '#2e7d32';
                }
                if (status === 'confusion') {
                  icon = '';
                  bgColor = '#ff9800';
                }
                
                return (
                  <div key={status} style={{
                    width: '20px',
                    height: '20px',
                    borderRadius: '50%',
                    backgroundColor: bgColor,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    fontSize: '12px',
                    border: '1px solid #7e4ab8',
                    position: 'relative'
                  }}>
                    {icon}
                    {data.duration && (
                      <span style={{
                        position: 'absolute',
                        top: '-8px',
                        right: '-8px',
                        backgroundColor: '#2c1810',
                        color: 'white',
                        borderRadius: '50%',
                        width: '14px',
                        height: '14px',
                        fontSize: '10px',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        border: '1px solid #7e4ab8'
                      }}>
                        {data.duration}
                      </span>
                    )}
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
      
      {/* UI */}
      {renderSkillsUI()}
      
      {/*  */}
      <div className="combat-logs" style={{
        maxHeight: '150px',
        overflowY: 'auto',
        backgroundColor: '#2c1810',
        border: '2px solid #5d3494',
        borderRadius: '8px',
        padding: '10px',
        marginTop: '15px'
      }}>
        {combatLogs.map((log, index) => {
          // 
          const isCritical = log.includes('CRITICAL!');
          const isEvade = log.includes('EVADE!');
          const isSkill = log.includes('') || log.includes('');
          const isStatus = log.includes('') || log.includes('') || log.includes('') || log.includes('');
          
          // 
          const displayLog = log
            .replace('CRITICAL! ', '')
            .replace('EVADE! ', '');
          
          return (
            <div key={index} style={{
              padding: '4px 0',
              borderBottom: index < combatLogs.length - 1 ? '1px solid #4c2a85' : 'none',
              color: isCritical ? '#f44336' : 
                    isEvade ? '#4caf50' : 
                    isSkill ? '#2196f3' :
                    isStatus ? '#ff9800' : '#e0e0e0',
              fontWeight: isCritical || isEvade || isSkill ? 'bold' : 'normal'
            }}>
              {displayLog}
              {isCritical && (
                <span style={{ 
                  marginLeft: '5px', 
                  color: '#f44336',
                  fontSize: '12px',
                  fontWeight: 'bold'
                }}>
                  !
                </span>
              )}
              {isEvade && (
                <span style={{ 
                  marginLeft: '5px', 
                  color: '#4caf50',
                  fontSize: '12px',
                  fontWeight: 'bold'
                }}>
                  !
                </span>
              )}
            </div>
          );
        })}
        <div ref={logsEndRef} />
      </div>
      
      {/*  */}
      <DropAnimation />
      
      <style jsx>{`
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        
        @keyframes bounceIn {
          0% {
            opacity: 0;
            transform: scale(0.3);
          }
          50% {
            transform: scale(1.05);
          }
          70% {
            transform: scale(0.9);
          }
          100% {
            opacity: 1;
            transform: scale(1);
          }
        }
        
        @keyframes damage-float {
          0% {
            opacity: 1;
            transform: translateY(0) translateX(-50%);
          }
          100% {
            opacity: 0;
            transform: translateY(-20px) translateX(-50%);
          }
        }
        
        .player.attacking {
          animation: attack-right 0.5s ease-in-out;
        }
        
        .monster.attacking {
          animation: attack-left 0.5s ease-in-out;
        }
        
        @keyframes attack-right {
          0% { transform: translateX(0); }
          50% { transform: translateX(20px); }
          100% { transform: translateX(0); }
        }
        
        @keyframes attack-left {
          0% { transform: translateX(0); }
          50% { transform: translateX(-20px); }
          100% { transform: translateX(0); }
        }
      `}</style>
    </div>
  );
};

export default CombatSystem;